nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #1b2430;
  padding: 20px 80px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
}
.navbar {
  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar li {
  list-style: none;
  padding: 0 20px;
  position: relative;
}

.navbar li a {
  text-decoration: none;
  color: white;
  font-size: 1.3rem;
  font-weight: 600;
  transition: 0.3s ease-in-outs;
}

.navbar li a:hover,
.navbar li a.active {
  color: #17cf97;
}
.navbar li a:hover::after,
.navbar li a.active::after {
  content: "";
  width: 30px;
  height: 2px;
  background-color: #17cf97;
  position: absolute;
  bottom: -4px;
  left: 20px;
}
.mobile {
  display: none;
}
.mobile i {
  color: white;
}

@media screen and (max-width: 769px) {
  .navbar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    top: 68px;
    right: -300px;
    width: 300px;
    height: 100vh;
    background-color: #2a3239;
    box-shadow: 0px 40px 60px rgba(0, 0, 0, 0.01);
    padding: 40px 0px 0px 10px;
    transition: 0.3s ease-in-out;
  }
  .active {
    right: 0px;
  }
  .navbar li {
    margin-bottom: 25px;
  }
  .mobile {
    display: block;
  }
  .mobile i {
    font-size: 24px;
    cursor: pointer;
  }
}
